

.OpenBoxContainer{
	position: absolute;
	max-height: 275px;
	width: 190px;
	background: var(--hrm_palette-general-background-b);
	transition: opacity 0.05s ease-in;
	opacity: 0;
    border: 1px solid var(--color_text_dad);
	border-radius: 8px;
	overflow-y: auto;
	box-shadow: 0 2px 4px rgb(0 0 0 / 20%);
	z-index: 9;
	top: 0px;
	
} 




.monthList{
	/*
	flex-wrap: wrap;
	*/

}

.filter_month_btn {
    padding: 6px 6px 6px 12px;
    border: 1px solid var(--hrm_palette-blue-purple_100);
    border-radius: 7px;
	align-items: center;
	cursor: pointer;
}



.filter_month_btn:hover {
   background-color: var(--hrm_palette-blue-purple_10);
	border: 1px solid var(--hrm_palette-blue-purple);
}

.filter_month_btn .lbl {
    font: 600 14px/24px var(--font_family);
	color: var(--hrm_palette-blue-purple);
	text-align: center;
	margin: 0 auto;
}


.filter_month_item {
    padding: 6px;
    border-radius: 2px;
	margin: 3px 0px;
	align-items: center;
	cursor: pointer;
	min-height: 30px;
}

.filter_month_item:hover {
	background-color: var(--hrm_palette-blue-purple_10);
 }

 .filter_month_item .lbl {
    font: 500 14px/24px var(--font_family);
	color: var(--hrm_palette-blue-purple);
	text-align: left;
	margin: 0 auto;
	white-space: nowrap;
}



.overlayDPD{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 7;

}



#drpdwn_click_outside[open] .filter_month_btn::before {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 8;
    display: block;
    cursor: default;
    content: " ";
    background: transparent;
}

#drpdwn_click_outside[open] .OpenBoxContainer[direction=left]{
	left: 0;
}

#drpdwn_click_outside[open] .OpenBoxContainer[direction=right]{
	right: 0;
}


#drpdwn_click_outside[open] .OpenBoxContainer{
	transition: opacity 0.25s ease-in;
	opacity: 1;
}





.month_picker[open]  .filter_month_btn::before {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 8;
    display: block;
    cursor: default;
    content: " ";
    background: transparent;
}

.month_picker[open] .OpenBoxContainer[direction=left]{
	left: 0;
}

.month_picker[open] .OpenBoxContainer[direction=right]{
	right: 0;
}

.month_picker[open] .OpenBoxContainer{
	transition: opacity 0.25s ease-in;
	opacity: 1;
}
